$light-orange: #fae4d2;

// index.scss is for CSS declarations that aren't specific to a component.
// In practice, that means this file is used for two things:
//
//   1. Setting the style of the <body> element
//   2. Loading webfonts

body {
  margin: 0;
  padding: 0;
  background-color: $light-orange !important;
}
